}
.preloader #main #contents .loader .info span:first-child{
    text-align:left;
     
}
.preloader #main #contents .loader .info span:last-child{
    text-align:right;
     
}

.preloader #main #contents .wrap{
    font-family:'Oswald';
    font-weight:bold;
    text-align:center;
    font-size:18px;
