    bottom:7px;
}
.vidContent{
    animation:fadeInLeft 2s ease-in-out;
    animation-delay:1.5s;
    position:absolute;
    top:290px;
    color:#f1f1f1;
    width:100%;
    padding:10px;
    text-align:left;
}
.vidContent h1{
